<template>
	<view class="created-1">
		<tabTop left-icon="back"  right-text="测试" title="选择低层公链"/>
		<view class="created-lists">
			<view class="created-lsit" v-for="(item,index) in lists" :key='index'>
				<view class="icon"></view>
				<view class="val">{{item.val}}</view>
				<view class="check" @click="onCheck(index)">
					<view class="check-icon" v-if="item.check"></view>
				</view>
			</view>
		</view>
		<nextButton :bottom='true' />
	</view>
</template>

<script>
	import tabTop from '@/components/tabTop.vue';
	import nextButton from '@/components/nextButton.vue';
	export default{
		components: {tabTop,nextButton},
		data(){
			return{
				lists:[
					{
						val:'REON',
						check:false
					},
					{
						val:'REON',
						check:false
					},
					{
						val:'REON',
						check:true
					}
				]
			}
		},
		
		methods:{
			onCheck(index){
				this.lists[index].check = !this.lists[index].check;
			}
		}
	}
</script>

<style lang="scss">
	.created-1{
		.created-lsit{
			@include size(auto,100rpx,#1D2B49);
			@include font(24rpx,#FFFFFF);
			display: flex;
			align-items: center;
			position: relative;
			.icon{
				@include size(50rpx,50rpx,#2DE9B7);
				border-radius: 50%;
				margin-left: 45rpx;
			}
			.val{
				margin-left: 16rpx;
			}
			.check{
				@include size(34rpx,34rpx,#2DE9B7);
				@include cen();
				border-radius: 50%;
				position: absolute;
				right: 40rpx;
				
				.check-icon{
					@include size(24rpx,24rpx,#FFF);
					border-radius: 50%;
				}
			}
		}
		
		.next{
			@include size(100%,66rpx,none);
			position: fixed;
			bottom: 83rpx;
			display: flex;
			justify-content: center;
			.next-button{
				@include size(500rpx,66rpx,none);
				@include font(24rpx,#fff);
				@include cen();
				background: linear-gradient(90deg,#2A65BC,#079D74);
				border-radius: 4rpx;
			}
		}
	}
</style>
